"use client"
import { useAuth } from "@/contexts/AuthContext"
import { useEffect, useState } from "react"
import { Clock, Calendar, Play, Square, MapPin, Timer } from "lucide-react"
import { DashboardLayout } from "@/components/dashboard-layout"
import { ProtectedRoute } from "@/components/ProtectedRoute"

export default function AttendancePage() {
  return (
    <ProtectedRoute>
      <DashboardLayout>
        <AttendancePageContent />
      </DashboardLayout>
    </ProtectedRoute>
  )
}

function AttendancePageContent() {
  const { user } = useAuth()
  const [todayAttendance, setTodayAttendance] = useState(null)
  const [attendanceHistory, setAttendanceHistory] = useState([])
  const [loading, setLoading] = useState(true)
  const [isCheckedIn, setIsCheckedIn] = useState(false)

  useEffect(() => {
    fetchAttendanceData()
  }, [])

  const fetchAttendanceData = async () => {
    try {
      const [todayRes, historyRes] = await Promise.all([
        fetch('http://localhost:5000/api/attendance/today', {
          headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        }),
        fetch('http://localhost:5000/api/attendance/history', {
          headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        })
      ])

      if (todayRes.ok) {
        const todayData = await todayRes.json()
        setTodayAttendance(todayData.data.attendance)
        setIsCheckedIn(todayData.data.hasCheckedIn)
      }

      if (historyRes.ok) {
        const historyData = await historyRes.json()
        setAttendanceHistory(historyData.data.attendance)
      }
    } catch (error) {
      console.error('Error fetching attendance data:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleCheckIn = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/attendance/check-in', {
        method: 'POST',
        headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
      })

      if (response.ok) {
        fetchAttendanceData()
      }
    } catch (error) {
      console.error('Error checking in:', error)
    }
  }

  const handleCheckOut = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/attendance/check-out', {
        method: 'POST',
        headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
      })

      if (response.ok) {
        fetchAttendanceData()
      }
    } catch (error) {
      console.error('Error checking out:', error)
    }
  }

  const formatTime = (dateString) => {
    if (!dateString) return 'N/A'
    return new Date(dateString).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-background p-6">
        <div className="max-w-7xl mx-auto">
          <div className="flex items-center justify-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary"></div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="p-6">
      <div className="max-w-7xl mx-auto space-y-8">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-light tracking-tight text-foreground">Attendance</h1>
            <p className="text-muted-foreground mt-1">Track your work hours and attendance</p>
          </div>
          <div className="text-right">
            <p className="text-sm text-muted-foreground">Today</p>
            <p className="text-lg font-medium text-foreground">
              {new Date().toLocaleDateString('en-US', {
                weekday: 'long',
                year: 'numeric',
                month: 'long',
                day: 'numeric'
              })}
            </p>
          </div>
        </div>

        {/* Today's Attendance Card */}
        <div className="bg-card border border-border rounded-lg p-8">
          <div className="text-center mb-8">
            <div className="w-20 h-20 bg-primary/10 rounded-full flex items-center justify-center mx-auto mb-4">
              <Clock className="w-10 h-10 text-primary" />
            </div>
            <h2 className="text-2xl font-semibold text-foreground mb-2">Today's Attendance</h2>
            <p className="text-muted-foreground">
              {isCheckedIn ? 'You are currently checked in' : 'Ready to start your workday'}
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
            <div className="text-center p-4 bg-muted/50 rounded-lg">
              <p className="text-sm text-muted-foreground mb-1">Check In</p>
              <p className="text-xl font-semibold text-foreground">
                {todayAttendance?.checkIn?.time ? formatTime(todayAttendance.checkIn.time) : '--:--'}
              </p>
              {todayAttendance?.checkIn?.isLate && (
                <p className="text-xs text-red-600 mt-1">Late by {todayAttendance.checkIn.lateMinutes} min</p>
              )}
            </div>

            <div className="text-center p-4 bg-muted/50 rounded-lg">
              <p className="text-sm text-muted-foreground mb-1">Check Out</p>
              <p className="text-xl font-semibold text-foreground">
                {todayAttendance?.checkOut?.time ? formatTime(todayAttendance.checkOut.time) : '--:--'}
              </p>
              {todayAttendance?.checkOut?.isEarly && (
                <p className="text-xs text-orange-600 mt-1">Early by {todayAttendance.checkOut.earlyMinutes} min</p>
              )}
            </div>

            <div className="text-center p-4 bg-muted/50 rounded-lg">
              <p className="text-sm text-muted-foreground mb-1">Total Hours</p>
              <p className="text-xl font-semibold text-foreground">
                {todayAttendance?.totalHours ? `${todayAttendance.totalHours}h` : '--'}
              </p>
              {todayAttendance?.overtimeHours > 0 && (
                <p className="text-xs text-green-600 mt-1">+{todayAttendance.overtimeHours}h overtime</p>
              )}
            </div>
          </div>

          <div className="flex justify-center">
            {!isCheckedIn ? (
              <button
                onClick={handleCheckIn}
                className="flex items-center gap-3 px-8 py-4 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors text-lg font-medium"
              >
                <Play className="w-6 h-6" />
                Check In
              </button>
            ) : !todayAttendance?.checkOut?.time ? (
              <button
                onClick={handleCheckOut}
                className="flex items-center gap-3 px-8 py-4 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors text-lg font-medium"
              >
                <Square className="w-6 h-6" />
                Check Out
              </button>
            ) : (
              <div className="text-center">
                <p className="text-green-600 font-medium">âœ“ Workday completed</p>
                <p className="text-sm text-muted-foreground mt-1">
                  Checked out at {formatTime(todayAttendance.checkOut.time)}
                </p>
              </div>
            )}
          </div>
        </div>

        {/* Attendance History */}
        <div className="bg-card border border-border rounded-lg overflow-hidden">
          <div className="px-6 py-4 border-b border-border">
            <h2 className="text-lg font-medium text-foreground">Attendance History</h2>
            <p className="text-sm text-muted-foreground">Your recent attendance records</p>
          </div>

          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-muted/50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                    Date
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                    Check In
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                    Check Out
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                    Total Hours
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                    Status
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-border">
                {attendanceHistory.map((record) => (
                  <tr key={record._id} className="hover:bg-muted/50">
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-foreground">
                      {new Date(record.date).toLocaleDateString()}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-foreground">
                      {record.checkIn?.time ? formatTime(record.checkIn.time) : '--:--'}
                      {record.checkIn?.isLate && (
                        <span className="text-red-600 text-xs ml-2">(Late)</span>
                      )}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-foreground">
                      {record.checkOut?.time ? formatTime(record.checkOut.time) : '--:--'}
                      {record.checkOut?.isEarly && (
                        <span className="text-orange-600 text-xs ml-2">(Early)</span>
                      )}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-foreground">
                      {record.totalHours ? `${record.totalHours}h` : '--'}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`inline-flex px-2 py-1 text-xs font-medium rounded-full ${
                        record.status === 'present'
                          ? 'bg-green-100 text-green-800'
                          : record.status === 'late'
                          ? 'bg-yellow-100 text-yellow-800'
                          : record.status === 'early_out'
                          ? 'bg-orange-100 text-orange-800'
                          : 'bg-red-100 text-red-800'
                      }`}>
                        {record.status.replace('_', ' ')}
                      </span>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {attendanceHistory.length === 0 && (
            <div className="text-center py-12">
              <Calendar className="w-16 h-16 text-muted-foreground mx-auto mb-4 opacity-50" />
              <h3 className="text-lg font-medium text-foreground mb-2">No attendance records</h3>
              <p className="text-muted-foreground">Your attendance history will appear here</p>
            </div>
          )}
        </div>

        {/* Quick Stats */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          <div className="bg-card border border-border rounded-lg p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-muted-foreground">This Month</p>
                <p className="text-2xl font-light text-foreground">
                  {attendanceHistory.filter(record => {
                    const recordDate = new Date(record.date)
                    const now = new Date()
                    return recordDate.getMonth() === now.getMonth() &&
                           recordDate.getFullYear() === now.getFullYear()
                  }).length} days
                </p>
              </div>
              <Calendar className="w-8 h-8 text-muted-foreground" />
            </div>
          </div>

          <div className="bg-card border border-border rounded-lg p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-muted-foreground">Avg Hours/Day</p>
                <p className="text-2xl font-light text-foreground">
                  {attendanceHistory.length > 0
                    ? (attendanceHistory.reduce((sum, record) => sum + (record.totalHours || 0), 0) / attendanceHistory.length).toFixed(1)
                    : '0'
                  }h
                </p>
              </div>
              <Timer className="w-8 h-8 text-blue-500" />
            </div>
          </div>

          <div className="bg-card border border-border rounded-lg p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-muted-foreground">On Time</p>
                <p className="text-2xl font-light text-foreground">
                  {attendanceHistory.filter(record => record.status === 'present').length}
                </p>
              </div>
              <Clock className="w-8 h-8 text-green-500" />
            </div>
          </div>

          <div className="bg-card border border-border rounded-lg p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-muted-foreground">Late Days</p>
                <p className="text-2xl font-light text-foreground">
                  {attendanceHistory.filter(record => record.checkIn?.isLate).length}
                </p>
              </div>
              <Clock className="w-8 h-8 text-red-500" />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}